@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&family=Sora:wght@200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


*,*::before,*::after {
  box-sizing: border-box;
  scrollbar-color: #6969dd #e0e0e0;

}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.header .fullscreen {
  background-color: #1e1f2b;
  position: fixed;
  left: 0;
  display: flex;
  align-items: center;
  height: 100vh;
  max-height: 100%;
  width: 120px;
  z-index: 1;
  transition: width ease-in-out 300ms;
  overflow: hidden;
}

.header .fullscreen h1 {
  margin: 0;
}

.header .fullscreen ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.header .fullscreen .links {
  display: flex;
  align-items: center;
  width: 115px;
  overflow: hidden;
  padding: 10px 0;
  margin: 10px 0;
  font-family: 'Poppins', sans-serif;
  color: white;
  font-size: 1rem;
  border-radius: 6px;
  transition: background-color ease 250ms,width ease-in-out 300ms;
}

.header .fullscreen .links:hover {
  background-color: #3a3c53;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.header .fullscreen .links-group {
  position: absolute;
  left: 20px;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 1vh;
}

.header .fullscreen .links-group .svg {
  margin: 10px;
}

.header .fullscreen .resume-link {
  text-decoration: none;
  color: white;
}

.header .fullscreen .bracket {
  color: white;
  font-size: 2rem;
}

.header .fullscreen .header-name {
  opacity: 0;
  transition: 150ms opacity 100ms;
}

.header .fullscreen:hover {
  width: 250px;
}

.header .fullscreen:hover .header-name {
  opacity: 1;
}

.header .fullscreen:hover .links {
  width: 200px;
}

.header .mobile {
  display: none;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  position: absolute;
  right: 0;
  padding: 10px 15px;
}

@media only screen and (max-width: 850px) {
  .header .fullscreen {
    display: none;
  }
  .header .mobile {
    display: block;
  }
}
